<template>
  <div class="hello">
    <el-form label-width="20%">
      <el-form-item label="">
        <el-col :span="20">
        <el-input  disabled v-model="address"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="10">
          <el-radio v-model="type" label="1">ETH</el-radio>
        </el-col>
        <el-col :span="10">
          <el-radio v-model="type" label="2"></el-radio>
        </el-col>
      </el-form-item>
      <el-form-item v-if="type==='2'" label="">
        <el-col :span="20">
          <el-input  v-model="contractAddr"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="20">
          <el-input  v-model="toAddr" placeholder=""></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="   ">
        <el-col :span="20">
          <el-input  v-model="transferAmt" placeholder=""></el-input>
        </el-col>
      </el-form-item>
      <el-form-item >

        <el-col :span="10">
          <el-button type="primary"
                     size="small"
                     @click="connect"></el-button>
        </el-col>
        <el-col :span="10">
        <el-button
                type="primary"
                size="small"
                @click="setAmt">   </el-button>
        </el-col>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
import walletUtils from '../utils/walletUtils'
export default {
  name: 'Main',
  data: function () {
    return {
      address: '',
      type: '1',
      contractAddr: '0xBCF060521B3578552D01ADB8A3FCDACF53C2CD1D',
      toAddr: '0xDB7B74D69D87CAC732EFFEBF45E851C1C076DCFB',
      transferAmt: 0

    }
  },
  created () {

  },
  methods: {
    connect () {
      let that = this
      walletUtils.checkApproveMetaMask(function (accounts) {
        console.log('' + accounts[0])
        that.reset()
        this.address = accounts[0]
      }, function (networkId) {
        console.log(' id:' + networkId)
        that.reset()
      }).then(res => {
        that.address = res
      })
    },
    setAmt () {
      if (!walletUtils.checkWeb3()) {
        this.$message.warning('')
        return
      }

      if (this.toAddr && this.transferAmt !== 0) {
        if (this.type === '1') {
          walletUtils.sendTransaction(this.toAddr, this.transferAmt, function (hash) {
            console.log('hash' + hash)
          }, function (receipt) {
            console.log('receipt' + receipt)
            // receipt,
            this.$message.success('')
          }, function (confirmationNumber, receipt) {
            console.log('confirmation confirmationNumber' + confirmationNumber + ' receipt:' + receipt)
            // N
          }, function (err) {
            console.log(' ' + err)
            // 
          })
        } else {
          walletUtils.sendContract(this.toAddr, this.transferAmt, this.contractAddr, function (hash) {
            console.log('hash' + hash)
          }, function (receipt) {
            console.log('receipt' + receipt)
            // receipt,
            this.$message.success('')
          }, function (confirmationNumber, receipt) {
            console.log('confirmation confirmationNumber' + confirmationNumber + ' receipt:' + receipt)
            // N
          }, function (err) {
            console.log(' ' + err)
            // 
          })
        }
      } else {
        this.$message.warning('')
      }
    },
    reset () {
      this.transferAmt = 0
      this.address = ''
      this.contractAddr = ''
      this.toAddr = ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



