//  web3
async function loadWeb3Info () {
  //  
  //   accounts[0]
  let curBlockNum = await web3.eth.getBlockNumber()
  let curAccount = await web3.eth.getAccounts()
  d('eth obj:' + web3.eth.toString())
  d(' ' + curBlockNum)
  d(' ' + curAccount.length > 0 ? curAccount[0] : '')
  d('metamask' + web3.currentProvider.isMetaMask)
  d(' ' + web3.eth.coinbase)
}

function d (any) {
  console.info(any)
}

//  
async function checkApproveMetaMask (accountChangeCall, networkChangeCall) {
  // this returns the provider, or null if it wasn't detected
  const provider = await detectEthereumProvider();
  if (!provider) {
    alert('Please install MetaMask!')
    return
  }
  if (provider !== window.ethereum) {
    alert('provider !== window.ethereum! provider:'+provider);
    return
  }
  //  Modern dapp browsers...
  if (provider) { //  
    web3 = new Web3(provider)
    try {
      //  Request account access if needed
      await window.ethereum.enable()
      let currentChainId = ethereum.chainId;
      // 
      try {
        provider.on('accountsChanged',  function (accounts) {
          curAccount = accounts[0]
          accountChangeCall(accounts)
        });
        provider.on('chainChanged', networkChangeCall);
      }catch (error1){
        console.log(error1)
      }

      //   
      curAccount = provider.selectedAddress
      await loadWeb3Info()
      return provider.selectedAddress
    } catch (error) {
      //  User denied account access...
      console.log(error)
      alert('metamask  error:'+error)//  
    }
  } else if (web3) { //  
    web3 = new Web3(web3.currentProvider)
    //  Acccounts always exposed
    await loadWeb3Info()
  } else {
    //   metamask
    console.log('Non-Ethereum browser detected. You should consider trying MetaMask!')
    alert('metamask')
  }
}

//  
function sendTransaction (to, amount, transHashCall, receiptCall, confirmCall, errorCall) {
  console.log('')
  let gasLimit = 21000//  2w1// $('#gasLimit').val()
  //  let gasPrice = 2000000000// 2gwwei, 90gwei, 180 ether  // $('#gasPrice').val()
  //  let nonce = $('#nonce').val()// 
  amount = web3.utils.toWei(amount)//  wei,web3
  //  gasPrice = web3.utils.toWei(gasPrice,'gwei')// 

  let message = {
    from: curAccount,
    to: to,
    value: amount,
    gasLimit: gasLimit
    //  nonce:nonce,
  }

  //   
  web3.eth.sendTransaction(message)
    .on('transactionHash', transHashCall)
    .on('receipt', receiptCall)
    .on('confirmation', confirmCall)
    .on('error', errorCall)
}

//  ()
function sendContract (to, amount, contractAddr, transHashCall, receiptCall, confirmCall, errorCall) {
  console.log('')
  let tokenAbi = contractAbi
  let gasLimit = 90000

  amount = web3.utils.toWei(amount)
  let message = {
    from: curAccount,
    to: contractAddr,
    value: 0,
    gasLimit: gasLimit
  }

  let contract = new web3.eth.Contract(tokenAbi, contractAddr)
  contract.methods.transfer(to, amount).send(message)
    .on('transactionHash', transHashCall)
    .on('receipt', receiptCall)
    .on('confirmation', confirmCall)
    .on('error', errorCall)
}

async function getEthBalance (address) {
  let balance = await web3.eth.getBalance(address)
  return fromWei(balance, 18)
}

async function getContractInfo (contractAddr, address) {
  //  
  let contract = await new web3.eth.Contract(contractAbi, contractAddr)
  //   ff token
  let name = await contract.methods.name().call().catch(catchException)
  //  / bat, FT
  let symbol = await contract.methods.symbol().call().catch(catchException)
  //  
  let decimals = await contract.methods.decimals().call().catch(catchException)
  console.log('decimals:' + decimals)
  //  
  let tokenBalance = await contract.methods.balanceOf(address).call().catch(catchException)
  //   decimals18
  let showBalance = fromWei(tokenBalance, decimals)

  return {
    name, symbol, decimals, tokenBalance, showBalance
  }
}


