async function getRound () {
  let round = 1
  let gameStartTime, now
  let contract = new web3.eth.Contract(BatConstants.gameABI, BatConstants.gameAddr)
  let sysResult = await contract.methods.getRound().call().catch(catchException)
  if (!varValid(sysResult)) {
    return null
  }
  // round
   round = sysResult[0]
  // start
   gameStartTime = sysResult[1]
  // now
   now = sysResult[2]

  round =fromWei(round,0);
  gameStartTime =fromWei(gameStartTime,0);
  now =fromWei(now,0);

  return {
    round, gameStartTime, now
  }
}

