async function uniSimpleShowInfo(userAddr) {
  let obj = await uniShowInfo(userAddr);
  let lpBalance= 0
  let lpInvest= obj.lpAmount;
  let aumNum= obj.bonus;

  //
  let uniContract = new web3.eth.Contract(BatConstants.uniABI,BatConstants.uniAddr);
  let sysResult = await uniContract.methods.showAddrs().call().catch(catchException)
  let needApprove = false
  if(varValid(sysResult) && sysResult[0] !== BatConstants.emptyAddr){
    let lpContract = new web3.eth.Contract(BatConstants.erc20ABI,sysResult[0]);
    let lpBalanceWei = await lpContract.methods.balanceOf(userAddr).call().catch(catchException)
    lpBalance = fromWei(lpBalanceWei);

    let appValueWei = await lpContract.methods.allowance(curAccount,BatConstants.uniAddr).call().catch(catchException);
    let tAv = new BigNumber(appValueWei);
    needApprove = tAv.lte(0)
  }
  return {
    lpBalance,
    lpInvest,
    aumNum,
    needApprove
  }
}

