async function sendUniAppr(userAddr, transHashCall, receiptCall, confirmCall, errorCall) {

  let uniContract = new web3.eth.Contract(BatConstants.uniABI,BatConstants.uniAddr);
  let sysResult = await uniContract.methods.showAddrs().call().catch(catchException)
  let lpTokenAddr = '';
  if(varValid(sysResult) && sysResult[0] !== BatConstants.emptyAddr){
    lpTokenAddr = sysResult[0];
  }else{
    return "";//
  }

  let gameAddr = ''
  let message = {
    from: userAddr,
    to: lpTokenAddr,
    value: 0
    //  gasLimit: gasLimit// metamask
  }
  let appValue = toWei("10000000000");
  let contract = new web3.eth.Contract(BatConstants.erc20ABI,lpTokenAddr)
  contract.methods.approve(BatConstants.uniAddr,appValue).send(message)// TODO 
    .on('transactionHash', transHashCall)// hash,hash,
    .on('receipt', receiptCall)// 
    .on('confirmation', confirmCall)//  function(num,receipt)num==1
    .on('error', errorCall)// 
}

// 
async function checkEthEnough (from, ethValueWei) {
  try {
    let balance = await web3.eth.getBalance(from)
    let bn = new BigNumber(balance)
    let ew = new BigNumber(ethValueWei)
    return bn.gte(ew)// 
  } catch (e) {

  }
  return false
}


async function checkHashStatus(hash){
  let obj = await web3.eth.getTransactionReceipt(hash).catch(catchException);
  if(!varValid(obj)){
    return 0
  }
  if (obj.status){
     return 1
  }else{
     return -1
  }
}


function varValid (any) {
  return any !== null && any !== undefined
}

function fromWei (value, decimals = 18) {
  let divisor = 10 ** decimals
  if(new BigNumber(value).div(divisor) % 1 > 0){
    return new BigNumber(value).div(divisor).toFixed(2)
  } else {
    return new BigNumber(value).div(divisor).toString(10)
  }
}

/**
 *
 * @param value string/number
 * @param decimals number
 * @returns string
 */
//  eslint-disable-next-line no-unused-vars
function toWei (value, decimals = 18) {
  let divisor = 10 ** decimals
  return new BigNumber(value).multipliedBy(divisor).toString(10)
}

function catchException (exception) {
  console.log('' + JSON.stringify(exception))
}

const wallet = {
  checkWeb3: checkWeb3,
  loadWeb3Info: loadWeb3Info,
  checkApproveMetaMask: checkApproveMetaMask,
  sendTransaction: sendTransaction,
  sendContract: sendContract,
  getEthBalance: getEthBalance,
  getContractInfo: getContractInfo,
  getContractTime:getContractTime,
  showSysInfo,
  showUserInfoNew,
  showSmInfo,
  showRankInfo,
  sendTestSm,
  sendWithdrawSmInvest,
  sendTestInvest,
  sendWithdrawBonus,
  uniShowInfo,
  uniShowOthers,
  petShowInfo,
  uniSimpleShowInfo,
  sendUniInvestLp,
  sendUniWithdrawBonus,
  sendUniAppr,
  getRound,
  checkHashStatus
}
export default wallet



