
import Vue from 'vue' // vue
import VueI18n from 'vue-i18n' // i18n
import Cookies from 'js-cookie' // cookie
import locale from 'element-ui/lib/locale' // elementuiui

import elementEnLocale from 'element-ui/lib/locale/lang/en' // element-ui
import elementZhLocale from 'element-ui/lib/locale/lang/zh-CN'// element-ui
import elementJaLocale from 'element-ui/lib/locale/lang/ja' // element-ui
import elementKoLocale from 'element-ui/lib/locale/lang/ko'// element-ui

import enLocale from './locale/en' // 
import zhLocale from './locale/zh' // 
import jaLocale from './locale/ja' // 
import koLocale from './locale/ko' // 
Vue.use(VueI18n) // vuei18n

// 
const messages = {
  en: {
    ...enLocale, // es6
    ...elementEnLocale
  },
  zh: {
    ...zhLocale,
    ...elementZhLocale
  },
  ja: {
    ...jaLocale,
    ...elementJaLocale
  },
  ko: {
    ...koLocale,
    ...elementKoLocale
  }
}
// 
const i18n = new VueI18n({
  locale: Cookies.get('language') || 'zh', // set locale
  messages // set locale messages
})
locale.i18n((key, value) => i18n.t(key, value))
export default i18n



