<template>
    <div class="main-div">
        <el-row :gutter="10" class="padding-tb">
            <el-col :xs="2" :sm="6" :md="10" :lg="16" :xl="18">
                <span>&nbsp;</span>
            </el-col>
            <el-col :xs="22" :sm="18" :md="14" :lg="8" :xl="8">
                <el-col :span="8">
                     <el-popover
                             placement="bottom"
                             width="360"
                             trigger="click">
                    <div>
                        <div></div>
                        <div></div>
                        <div>&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: red"></span></div>
                        <div>&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: skyblue"></span></div>
                    </div>
                    <el-button type="primary" slot="reference"></el-button>
                </el-popover>
                </el-col>
                <el-col :span="8">
                     <el-button type="primary" @click="linkWallet" ></el-button>
                </el-col>
                <el-col :span="8">
                    <div class="mini-font">
                        <div class="mini-font-cell">
                            <div :title="ethBalance"><span>ETH:</span><span>{{ethBalance}}</span></div>
                            <div :title="batBalance"><span>BAT:</span><span>{{batBalance}}</span></div>
                        </div>
                    </div>
                </el-col>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :span="4"  class="label-col" >
                <span class="label-span"></span>
            </el-col>
            <el-col :span="20" >
                <el-col :span="18" >
                    <el-input placeholder="ETH" v-model="ethNum"></el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" size="normal" @click="confirmFunding"></el-button>
                </el-col>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :span="4"  class="label-col"  >
                <span class="label-span"></span>
            </el-col>
            <el-col :span="20" >
                <el-col :span="18" >
                    <el-input placeholder="BAT" v-model="batNum"></el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" size="normal" @click="confirmPledge" ></el-button>
                </el-col>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :xs="10" :sm="6" :md="4" :lg="4" :xl="4" class="label-col" >
                <span class="label-span">:</span>
            </el-col>
        </el-row>
        <el-row :gutter="10">
            <el-col :offset="2" :span="18">
                <div class="label-col">: <span >{{ethTotal}}</span>ETH</div>
                <div class="label-col">: <span >{{userInvestTotal}}</span>BAT</div>
                <div class="label-col">: <span >{{userBonus}}</span>BAT</div>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :xs="10" :sm="6" :md="4" :lg="4" :xl="4" class="label-col" >
                <span class="label-span">:</span>
            </el-col>
        </el-row>
        <el-row :gutter="10">
            <el-col :offset="2" :span="18">
                <div class="label-col">: <span >{{userInvestTotal}}</span>ETH</div>
                <div class="label-col">: <span >{{userInvestTotal}}</span>BAT</div>
                <div class="label-col">: <span >{{userBonus}}</span>BAT</div>
                <div class="label-col">: <span >{{userBonus}}</span>ETH</div>
                <div class="label-col">: <span >{{userInvestTotal}}</span>BAT</div>
                <div class="label-col">: <span >{{userInvestTotal}}</span>BAT</div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import wallet from '../utils/walletUtils'
export default {
  name: 'BatView',
  data () {
    return {
      address: '',
      ethNum: '',
      ethBalance: '',
      batBalance: '',
      batNum: '',
      userInvestTotal: '',
      userBonus: '',
      ethTotal: '',
      contractAddr: '0xe22cDdC7F72c528f720f10fdcD84A2964aF77da2',
      gameAddr: '0xD247aF662D6B65985B93427FCa836a780bBcFa7B'
    }
  },
  methods: {
    async linkWallet () {
      let that = this
      that.address = await wallet.checkApproveMetaMask(function (accounts) {
        console.log('' + accounts[0])
        that.reset()
        this.address = accounts[0]
      }, function (networkId) {
        console.log(' id:' + networkId)
        that.reset()
      })
      that.ethBalance = await wallet.getEthBalance(that.address)
      console.log('' + that.address)
      let contractInfo = wallet.getContractInfo(that.contractAddr, that.address)
      that.batBalance = contractInfo.showBalance
      console.log('' + contractInfo.symbol)
      let gameInfo = wallet.getGameInfo(that.gameAddr, that.address)
      that.userInvestTotal = gameInfo.userInvestTotal
      that.userBonus = gameInfo.userBonus
    },
    confirmPledge () {

    },
    confirmFunding () {

    },
    reset () {
      this.address = ''
    }
  }
}
</script>

<style scoped>
    .padding-tb {
        padding: 10px 0;
    }
    .label-span{
        font-size: 20px;
        line-height: 40px;
    }
    .label-col{
        text-align: left;
        padding-left: 10px;
    }

    .mini-font{
        font-size: 12px;
        color: #2c3e50;
        text-align: left;
        height: 40px;
        display: table;
    }
    .mini-font-cell{
        display: table-cell;
        vertical-align: middle;
        text-align: center;
    }
    .el-col{
        border-radius: 4px;
    }

</style>



